// Micro Site Trips page styles

//Landing page
//Hero
.c-hero-ms__trips {
  background-color: $green;
}

//Sidebar list
.c-list-box-ms__trips {
  padding: 0;
  .c-list-box__heading {
    background: $green;
  }
  .c-list-box__content {
    background: $dark-green;
  }

  .c-list-box__subitem {
    &:focus {
      background: $very-dark-green;
    }

    .no-touchevents & {
      &:hover {
        background: $very-dark-green;
      }
    }
  }
}

.c-nav-list-ms__trips {
  .c-nav-list__item {
    .c-nav-list__link {
      background: $green;
      border-color: $green;

      &:focus,
      &.is-active {
        background: $dark-green;
        border-color: $dark-green;
      }

      .no-touchevents & {
        &:hover {
          background: $dark-green;
          border-color: $dark-green;
        }
      }
    }
  }
}

.c-block-list-ms__trips {
  .c-card__title {
    a {
      text-decoration: underline;
      &:focus {
        color: $green;
      }

      .no-touchevents & {
        &:hover {
          color: $green;
        }
      }
    }
  }

  .button--pill {
    &:first-child {
      background: $green;
      &:focus {
        background: $light-green;
      }

      .no-touchevents & {
        &:hover {
          background: $light-green;
        }
      }
    }
    background: $very-dark-green;

    &:focus {
      background: $green;
    }

    .no-touchevents & {
      &:hover {
        background: $green;
      }
    }
  }

  .c-card__sticker {
    background: $green;
  }

  .c-block-list-trips__title {
    margin-bottom: 0;
  }

  .c-card__link {
    &:hover {
      svg {
        fill: $very-dark-gray;
      }
    }
  }

  &.c-block-list-ms__trips--week {
    .button--pill {
      &:first-child {
        background: $very-dark-green;
        &:focus {
          background: $green;
        }

        .no-touchevents & {
          &:hover {
            background: $green;
          }
        }
      }
      background: $green;

      &:focus {
        background: $light-green;
      }

      .no-touchevents & {
        &:hover {
          background: $light-green;
        }
      }
    }
  }
}

//Location Details
.c-page__location--details {
  @include media($bp-large) {
    margin-top: rem-calc(70);
  }
}

//Button Pill
.button--pill--trip {
  background: $green;
  &:hover {
    background: $light-green;
  }

  &:focus {
    background: $dark-green;
  }

  .no-touchevents & {
    &:hover {
      background: $dark-green;
    }
  }
}

//WYSIWYG Style overrides
.c-page__content {
  h2 {
    margin-top: rem-calc(20);
    &:first-child {
      margin-top: 0;
    }
  }

  p {
    margin-bottom: rem-calc(15);
  }
}

//Google Map [ACF] styles
.acf-map {
  float: left;
  width: 100%;
  height: 400px;
  margin: rem-calc(40 0);
}

//Article Content
.o-article-trip {
  @include media($bp-large) {
    float: right;
  }

  //Detail page
  //Page Intro
  .c-page__intro,
  .c-page__content {
    margin-bottom: rem-calc(30);
  }

  .c-page__content {
    @include media($bp-large) {
      margin-bottom: 0;
    }
  }
}

//Article Sidebar
.o-article-trip-sidebar {
  @include media($bp-large) {
    float: left;
  }
}